import React from 'react'
import { Link } from "react-router-dom";

function Categorycard(props) {
    return (
        <div className="col-lg-4 col-md-6 mb-30">
            <div className="categories-items">
                <div className="cate-images">
                    <Link to="/course"><img src={props.CategoryImage} alt="category-image" /></Link>
                </div>
                <div className="contents">
                    <div className="img-part">
                        <img src={props.CategoryIcon} alt="category-icon" />
                    </div>
                    <div className="content-wrap">
                        <h2 className="title"><a href="#">{props.title}</a></h2>
                        <span className="course-qnty">{props.courseqnty}</span>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Categorycard;
